export default {
  name: 'PressProtocol',
  options: {
  },
  components: {
  },
  props: {
    list: {
      type: Array,
      default: () => ([]),
    },
    customStyle: {
      type: String,
      default: '',
    },
    customClass: {
      type: String,
      default: '',
    },
  },
  emits: ['click'],
  data() {
    return {
    };
  },
  computed: {
  },
  mounted() {
  },
  methods: {
    clickProtocol(infoItem) {
      if (!infoItem.link) return;
      this.$emit('click', infoItem);
    },
  },
};
